<template>
  <f7-page class="profile-edit">
    <template v-if="user">
      <div class="main-content">
        <f7-list no-hairlines-md>
          <f7-list-item>
            <f7-label>Full Name</f7-label>
            <f7-input :value="form.fullName" disabled class="disabled" type="text" @input="form.fullName = $event.target.value" />
          </f7-list-item>
          <f7-list-item>
            <f7-label>Email</f7-label>
            <f7-input :value="form.email" disabled class="disabled" type="email" @input="form.email = $event.target.value" />
          </f7-list-item>
          <f7-list-item>
            <f7-label>Bio</f7-label>
            <f7-input :value="form.bio" type="text" @input="form.bio = $event.target.value" />
          </f7-list-item>
          <f7-list-item>
            <f7-label>Emoji</f7-label>
            <f7-input :value="form.emoji" type="text" @input="form.emoji = $event.target.value" />
          </f7-list-item>
          <f7-list-item>
            <f7-label>Photo Url</f7-label>
            <f7-input :value="form.photoURL" type="url" @input="form.photoURL = $event.target.value" />
          </f7-list-item>
        </f7-list>
      </div>
      <div v-if="error" class="error-message">{{error}}</div>
      <div class="actions">
        <f7-button color="white" text-color="black" fill @click="save">Save</f7-button>
        <span class="separator">or</span>
        <f7-button color="white" text-color="black" fill @click="$f7router.navigate('/profile')">Go back</f7-button>
      </div>
    </template>
  </f7-page>
</template>
<script>
export default {
  name: 'ProfileEdit',
  data() {
    return {
      form: {
        fullName: '',
        email: '',
        emoji: '',
        photoURL: '',
        bio: '',
      },
      error: '',
    }
  },
  computed: {
    user() {
      return this.$root.$data.user
    },
  },
  created() {
    if (!this.user) {
      this.$f7router.navigate(`/`)
    } else {
      this.form = {
        fullName: this.user.fullname,
        email: this.user.email,
        emoji: this.user.emoji,
        photoURL: this.user.photoURL,
        bio: this.user.bio || '',
      }
    }
  },
  methods: {
    save() {
      this.$f7.dialog.preloader('Updating profile...')
      this.$fns
        .httpsCallable('updateProfile')({
          bio: this.form.bio,
          emoji: this.form.emoji,
          photoURL: this.form.photoURL,
        })
        .then(() => {
          this.$f7.dialog.close()
          const notif = this.$f7.notification.create({
            text: `Profile updated!`,
            closeTimeout: 3000,
            closeOnClick: true,
          })
          notif.open()
          this.$f7router.navigate(`/profile`)
        })
        .catch((error) => {
          this.error = error.message
          this.$f7.dialog.close()
        })
    },
  },
}
</script>

<style scoped>
.main-content {
  margin: 16px;
  padding: 16px 0;
  background-color: white;
}
.actions {
  display: flex;
  flex-direction: column;
  margin: 16px;
  padding: 16px 0;
}
.error-message {
  margin: 0 16px;
  color: #ff3b30;
  text-align: left;
  font-size: 12px;
}
h1 {
  margin-left: 16px;
}
.separator {
  color: #666;
}
</style>
